import fire from './fire';

var OpenSourceData = {
   all: function(response) {
      fire.ref('opensource').orderByChild('order').once('value', (snap) => {
         var data = []
         snap.forEach((child) => {
            data.push(child.val())
         })
         response(data)
      })
   }
}

export default OpenSourceData;



