import React, { Component } from 'react';
import Ring from './Ring/Ring'
import './Rings.css';
import MathD from '../../MathD'
import JourneyData from '../../models/journey';

const shownLevels = 3;

function sizeAtLevel(level) {
   // Not creazy about according these values, but don't really have choice
   // since I couldn't figure out the pattern between the values
   switch (level) {
      case 0: return 80
      case 1: return 55
      case 2: return 30
      default: return 80
   }
}

function createExperienceConnectors(items) {
   var connectors = [];
   for (var i=0; i < Object.keys(items).length; i++) {
      const key = Object.keys(items)[i]
      if (key.includes('employment') === false) continue
      const item = items[key]
      var x = 50 + MathD.sin(Number(item.length - 1) * 30) * 50
      var y = 50 - MathD.cos(Number(item.length - 1) * 30) * 50
      var point = "100% 100%, " + x + "% " + y + "%"

      const difference = (new Date()).getFullYear() - item.year

      connectors.push(
         <div className="item-employment-connector" key={key} style={{
            clipPath: "polygon(50% 0, 100% 0, " + point + ")",
            transform: "rotate(" + Number(item.start) * 30 + "deg)",
            width: sizeAtLevel(difference) + "%",
            height: sizeAtLevel(difference) + "%",
            borderColor: item.brandColor
         }}></div>
      )
   }
   return connectors
}

class Rings extends Component {

   insertLevels() {
     var rings = []
     for (var ring=0; ring < shownLevels; ring++) {
        const year = (new Date()).getFullYear() - ring;
        const items = JourneyData.filterForYear(year, this.props.items);
        rings.push(<Ring key={ring} level={ring} items={items} ringSelected={this.props.ringChange}/>)
     }
     return rings
  }

   render() {
      return (
         <div className="col-1-2 rings-content">
            <div className="rings">
               <p className="rings-selected-year">{this.props.selectedYear}</p>
               { this.insertLevels() }
               { createExperienceConnectors(this.props.items) }
            </div>
         </div>
      );
   }
}

export default Rings;



