import React from 'react';
import './ItemTypes.css';

function EmploymentItem(props) {
   return (
      <img
         className="item-employment"
         src={ window.location.origin + "/" + props.src }
         alt="Employment Icon"
      />
   )
}

export default EmploymentItem;



