import React, { Component } from 'react';
import OpenSourceData from '../../models/opensource';
import './OpenSource.css';

function GithubRepo(props) {
   return (
      <div className="repository">
         <a href={ props.src }>
            <p className="repository-title">{ props.title }</p>
            <img src={ "icons/" + props.lang + "LanguageIcon.png" }  alt="Language Icon" />
            <p className="repository-desc">{ props.desc }</p>
         </a>
      </div>
   )
}

class OpenSource extends Component {
   constructor(props) {
      super(props)
      this.state = { projects: [] }
   }
   componentDidMount() {
      OpenSourceData.all((data) => {
         this.setState({ projects: data })
      })
   }


   render() {
      const projects = this.state.projects
      var projectsElements = []

      if (projects !== undefined) {
         for (var i=0; i < projects.length; i++) {
            const item = projects[i]

            projectsElements.push(
               <GithubRepo key={item.name} desc={item.desc} lang={item.lang} title={item.name} src={item.src} />
            )
         }
      }

      return (
         <div className="col-1-1">
            <h3 className="section-header">Open Source</h3>
            { projectsElements }
         </div>
      )
   }
}

export default OpenSource;



