import React, { Component } from 'react';
import { Header, Journey, Projects, LaunchList, Footer, OpenSource } from './components/';
import './App.css';

class App extends Component {
   render() {
      return (
         <div className="grid grid-pad">
            <Header />
            <Journey />
            <Projects />
            <OpenSource />
            <LaunchList />
            <Footer />
         </div>
      );
   }
}

export default App;



