import React from 'react';
import './Footer.css';

function SocialIcon(props) {
   return (
      <a href={ props.link } target="_blank"><li>
         <img src={ window.location.origin + "/" + props.src} alt="Social Icon" />
      </li></a>
   )
}

function Footer(props) {
   return (
      <div className="col-1-1">
         <ul id="footer">
            <SocialIcon src="icons/Github.svg" link="https://github.com/kirkbyo" />
            <SocialIcon src="icons/Twitter.svg" link="https://twitter.com/kirkbyo_"/>
            <SocialIcon src="icons/Linkedin.svg" link="https://www.linkedin.com/in/kirkbyo/"/>
         </ul>
      </div>
   )
}

export default Footer;



