import React, { Component } from 'react';
import { Rings } from '../Rings';
import { Experience } from '../Experience';
import JourneyData from '../../models/journey';

class Journey extends Component {
   constructor(props) {
      super(props);
      this.state = {
         selectedYear: 2018,
         journey: {}
      }
      this.selectedYearChange = this.selectedYearChange.bind(this);
   }

   componentDidMount() {
      JourneyData.all((data) => {
         this.setState({ journey: data })
      })
   }

   selectedYearChange(year) {
      this.setState({ selectedYear: year })
   }

   render() {
      const experience = JourneyData.filterForYear(this.state.selectedYear, this.state.journey);

      return (
         <div className="col-1-1" style={{
            display: 'block'
         }}>
            <h3 className="section-header">Journey</h3>
            <Rings className="col-1-2" items={this.state.journey} selectedYear={this.state.selectedYear} ringChange={this.selectedYearChange} />
            <Experience className="col-1-2" experience={experience} selectedYear={this.state.selectedYear} selectedYearChanged={this.selectedYearChange} />
         </div>
      );
   }
}

export default Journey;



