import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown';
import './Experience.css';

function layoutItems(experience) {
   var items = [];
   const keys = Object.keys(experience)

   for (var i=0; i < keys.length; i++) {
      const item = experience[keys[i]];

      items.push(
         <p key={keys[i]}><b>{item.name}</b> <em className="exp-title">{item.subtitle}</em></p>
      )
      items.push(
         <ReactMarkdown className="exp-desc" key={keys[i] + "-desc"} source={item.description} />
      )
   }

   return items
}

function SelectorButtons(props) {
   var yearsButtons = []
   const yearDifference = (new Date()).getFullYear() - 2015
   for (var i=0; i < yearDifference; i++) {
      const year = (new Date()).getFullYear() - i;
      var className = ""
      if (props.selectedYear === year) className = "exp-year-selected"

      yearsButtons.push(
         <li key={"selector-" + year}><a className={className} onClick={props.onClick}>{year}</a></li>
      )
   }
   return yearsButtons
}

class Experience extends Component {
   constructor(props) {
      super(props)
      this.onClick = this.onClick.bind(this);
   }
   onClick(e) {
      if (e === undefined) return
      const year = Number(e.target.innerHTML)
      this.props.selectedYearChanged(year)
   }

   render() {
      return (
         <div className="col-1-2 exp">
            <ul className="exp-year">
               <SelectorButtons onClick={this.onClick} selectedYear={this.props.selectedYear}/>
            </ul>
            { layoutItems(this.props.experience) }
         </div>
      );
   }
}

export default Experience;



