import fire from './fire';

var JourneyData = {
   atYear: function(year, response) {
      fire.ref('journey').orderByChild("year").equalTo(year).once('value', (snap) => {
         response(snap.val())
      })
   },

   all: function(response) {
      fire.ref('journey').orderByChild("start").once('value', (snap) => {
         response(snap.val())
      })
   },

   filterForYear: function(year, data) {
      if (data === undefined) return
      var filtered = {}
      var keys = Object.keys(data)
      for (var i=0; i < keys.length; i++) {
         var item = data[keys[i]];

         if (item['year'] === year) {
            filtered[keys[i]] = item;
         }
      }
      return filtered
   }
}

export default JourneyData



