function toRadians(angle) {
  return angle * (Math.PI / 180);
}

function roundNum(number, decimals) {
    decimals = decimals || 5;
    return Math.round(number * Math.pow(10, decimals)) / Math.pow(10, decimals);
}

var MathD = {
    sin: function(number){
        return roundNum(Math.sin(toRadians(number)));
    },
    cos: function(number){
        return roundNum(Math.cos(toRadians(number)));
    },
    tan: function(number){
        return roundNum(Math.tan(toRadians(number)));
    }
};

export default MathD



