import React, { Component } from 'react';
import { EmploymentItem, AwardItem, ProjectItem } from '../ItemTypes'
import MathD from '../../../MathD'
import './Ring.css';

function yearFromLevel(level) {
   if (level === null) return null;
   var index = Number(level);
   return (new Date()).getFullYear() - index;
}

function RingSize(level) {
   return 100 - (30 * level);
}

function insertContent(key, item) {
   if (key.includes('employment')) {
      return <EmploymentItem src={ item.icon }/>
   }

   if (key.includes('award')) {
      return <AwardItem src={ item.icon } />
   }

   if (key.includes('project')) {
      return <ProjectItem src={ item.icon } />
   }

   return <p></p>
}

function create(items) {
   const itemsElements = [];

   for (var i=0; i < Object.keys(items).length; i++) {
      const key = Object.keys(items)[i]
      const item = items[key]
      const startX = 50 + MathD.sin(Number(item.start) * 30) * 50;
      const startY = 50 - MathD.cos(Number(item.start) * 30) * 50;

      itemsElements.push(
         <div key={key} style={{ top: (startY - 8.7) + "%", left: (startX - 8.7) + "%" }}>
            { insertContent(key, item) }
         </div>
      )
   }
   return itemsElements;
}

class Ring extends Component {
   constructor(props) {
      super(props);
      this.state = { items: {}, hovered: false }

      this.onMouseEnter = this.onMouseEnter.bind(this);
      this.onMouseLeave = this.onMouseLeave.bind(this);
   }

   onMouseEnter() {
      this.props.ringSelected(yearFromLevel(this.props.level));
      this.setState({ hovered: true });
   }

   onMouseLeave() {
      this.setState({ hovered: false });
   }

   render() {
      const size = RingSize(this.props.level);
      return (
         <div className="ring" style={{ width: (size + "%"), height: (size + "%")}}  onMouseEnter={this.onMouseEnter} onMouseLeave={this.onMouseLeave}>
            <div className="ring-inner">
               { create(this.props.items) }
            </div>
         </div>
      )
   }
}

export default Ring;



