import fire from './fire';

var ProjectsData = {
   all: function(response) {
      fire.ref('projects').orderByChild('order').once('value', (snap) => {
         var data = []
         snap.forEach((child) => {
            data.push(child.val())
         })
         response(data)
      })
   }
}

export default ProjectsData;



