import React, { Component } from 'react';
import ProjectsData from '../../models/projects';
import './Projects.css';

function MobileApp(props) {
   const project = props.project

   return (
      <a className="iOS-App" href={ project.link }>
         <img src={ window.location.origin + "/" + project.icon } alt={ project.name + " App Icon"} className="app-icon"/>
         <div>
            <h3 className="app-name">{ project.name }</h3>
            <h4 className="slogan">{ project.subtitle }</h4>
         </div>
      </a>
   )
}

class Projects extends Component {
   constructor(props) {
      super(props)
      this.state = { projects: [] }
   }
   componentDidMount() {
      ProjectsData.all((data) => {
         this.setState({ projects: data })
      })
   }

   render() {
      const projects = this.state.projects
      var projectsElements = []

      if (projects !== undefined) {
         for (var i=0; i < projects.length; i++) {
            const item = projects[i]

            projectsElements.push(
               <div key={item.name} className="col-1-3">
                  <MobileApp project={item} />
               </div>
            )
         }
      }

      return (
         <div className="col-1-1">
            <h3 className="section-header">Projects</h3>
            { projectsElements }
         </div>
      )
   }
}

export default Projects;



